import React from 'react'
import { Card, Button, CardHeader, CardBody } from 'reactstrap';

import { validateEmail, clear, validateFields, customValidation } from '../../common.js'
import axios from '../../API.js'

class Topworkers extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			id: '1', email: '', username: '', filepath: '', filename: '', myimage: null, isError: false, isUpdate: false,
			error: { email: '', username: '', filepath: '', filename: '' }
		}
	}
	onFileChange = (e) => {
		this.setState({ myimage: e.target.files[0] })
	}
	onChange = (event) => {
		const data = this.state
		data[event.target.name] = event.target.value
		this.setState({ data })
		if (data.isError)
			this.onValidation(data)
	}
	onValidation = (ref = '') => {
		const emptyFields = [
			{ key: 'email', errVal: 'Enter Email' },
			{ key: 'username', errVal: 'Enter Username' },
		]
		const data = validateFields(ref === '' ? this.state : ref, emptyFields)

		this.setState({ data })
		return data.isError
	}
	onFormSubmit = (e) => {
		e.preventDefault();
		const formData = new FormData();
		formData.append('myimage', this.state.file);
		const config = {
			headers: {
				'content-type': 'multipart/form-data'
			}
		};
		axios.post("/upload", formData, config)
			.then((response) => {
				alert("The file is successfully uploaded");
			}).catch((error) => {
			});
	}
	onSubmit = () => {
		const data = this.state
		if (!this.onValidation()) {
			const formData = new FormData();
			formData.append('id', this.state.id);
			formData.append('myimage', this.state.myimage);
			formData.append('email', this.state.email);
			formData.append('username', this.state.username);
				
			if (this.state.isUpdate) {
				let data = this.state
				data.action = 'update'
				data.userid = '1'
				formData.append('action', 'update');
				formData.append('userid', '1');
				console.log(...formData.entries())
				axios.post("/topworkers/Update", formData).then(res => {
					if (res.data.status === 'success') {
						this.props.history.push('/topworkers')
						alert('Topworkers Details Updated Successfully..!!')
					}
				})
			}
			else {
				let data = this.state
				data.workertype = 'work1'
				data.userid = '1'
				formData.append('action', 'add');
				formData.append('userid', '1');
				axios.post("/topworkers/Add", formData).then(res => {
					console.log(res)
					if (res.data.status === 'success')
						this.props.history.push('/topworkers')
					alert('Topworkers Details Added Successfully..!!')
				}).catch(err => {
					console.log(err)
				})
			}
		}
		// 	if(!res.isError) {
		// 		axios.post(this.props.common.serverURL+"/axios/Users/Add",data).then(res=>{
		// 			if(res.data.status==='success')
		// 				this.props.history.push({pathname:'/workerRegister',state:res.data.data.insertId})
		// 		}).catch(err=>{
		// 			console.log(err)
		// 		})
		// 	}
		// })
	}

	componentDidMount = () => {
		console.log(this.props.location.state)
		if (this.props.location.state !== undefined) {
			axios.get("/Topworkers", { params: { id: this.props.location.state.id } }).then(res => {
				let view = res.data.data[0];
				this.setState({ id: view.id, email: view.email, username: view.username, isUpdate: true })
			})
		}
		else {
			axios.get("/topworkers/Auto").then(res => {
				this.setState({ id: res.data[0].id })
			})
		}
	}

	render() {
		let propsTitle = !this.state.isUpdate ? "Add" : "Update";

		let view = (
			<>
				<div className="menubar">

					<div className="row text-left">
						<div className="col-2"></div>
						<div className="col-8">
							<Card>
								<CardHeader tag="h4">{propsTitle} Top Workers</CardHeader>
								<CardBody className="padding-20 font-weight-bold">

									<div className="form-group" style={{ display: 'none' }}>
										<label>Requester ID</label>
										<input name="id" type="text" className="form-control" disabled value={this.state.id} />
									</div>
									<div className="form-group">
										<label>Email</label>
										<input name="email" type="text" className="form-control" required onChange={this.onChange} value={this.state.email} />
										{this.state.error.email}
									</div>
									<div className="form-group">
										<label>Username</label>
										<input name="username" type="text" className="form-control" required onChange={this.onChange} value={this.state.username} />
										{this.state.error.username}
									</div>
									<div className="form-group">
										<label>File Upload</label>
										<input name="filename" type="file" className="form-control" required onChange={this.onFileChange} />
										{this.state.error.filename}
									</div>

									<div className="text-right">
										<Button color="primary" onClick={this.onSubmit}>Submit</Button>{'  '}
										<Button color="secondary" onClick={this.clear}>Clear</Button>
									</div>

								</CardBody>
							</Card>
						</div>
						<div className="col-2"></div>
					</div>
				</div>

			</>
		)
		return view;
	}
}
export default Topworkers;